*, *::before, *::after{
    box-sizing: border-box;
}
body{
    line-height: 1.5;
    font-family: "Noto Sans TC", "Roboto",sans-serif;
}
img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}
a{
    text-decoration: none;
}
.container{
    max-width: 1320px;
    margin: 0 auto;
    padding: 0 12px;
}
@include pad{
    .container{
        max-width: 720px;
    }
}
@include mobile{
    .container{
       max-width: 372px;
        padding: 0; 
    }
}
/*font*/
.fw-r{
    font-weight: 400;
}
/*flex*/
.df{
    display: flex;
}
.justify-cc{
    justify-content: center;
}
.justify-csbt{
    justify-content: space-between;
}
.align-ic{
    align-items: center;
}
/*padding&margin*/
.pl-64{
    padding-left: 64px;
}


/* header */
.header{
    background-color: $primary;
    color: #ffff;
    font-size: $f-sz-20;
    img{
        max-width: 80px;
    }
    a{
        color: #ffff;
    }
    li a{
        display: block;
        padding-top: 25px;
        padding-bottom: 25px;
    }
}
@include mobile{
    .header{
       &-box{
            max-width: 100%;
            flex-direction: column;
            align-items: center;
        }
        img{
            padding: 12px 0;
        }
        ul{
            max-width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            font-size: $f-sz-16;
        }
        li{ 
            max-width: 50%;
            border: 1px solid #ffff;
        }
        li a{
            display: block;
            text-align: center;
            padding: 12px 0;
        }
    }  
}
/* footer */
.footer{
    background-color: $primary;
    color: #ffff;
    a{
        color: #ffff;
    }
}

